.scores-container {
  display: flex;
  justify-content: space-around;
  padding: 20px;
  margin-top: 20px;
}

.winning-message {
  text-align: center;
  padding: 20px;
  font-size: 30px;
}

.genius-img {
  height: 100px;
  width: 100px;
}

.btn {
  text-align: center;
  display: block;
  margin: 0 auto;
  margin-bottom: 20px;
  padding: 10px 20px;
  border-radius: 10px;
  border: 1px solid #4338ca;
  font-family: 'Indie Flower', cursive;
  font-size: 25px;
}

.btn:hover {
  background-color: #4338ca;
  color: #fff;
  cursor: pointer;
  font-weight: bold;
}
